:root {
  --main-font: 'Nunito Sans', sans-serif;
  --background-color: hsl(0, 0%, 98%);
  --element-color: hsl(0, 0%, 100%);
  --text-color: hsl(200, 15%, 8%);
  --input-text-color: hsl(0, 0%, 52%);
  --container-width: 1200px;
  --main-shadow: 0 0.125rem 0.3125rem rgba(0, 0, 0, 0.07);
  --button-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.3); }

.dark-mode:root {
  --background-color: hsl(207, 26%, 17%);
  --element-color: hsl(209, 23%, 22%);
  --text-color: hsl(0, 0%, 100%);
  --input-text-color: hsl(0, 0%, 100%); }

html {
  box-sizing: border-box;
  font-size: 100%; }

*,
*::after,
*::before {
  box-sizing: inherit; }

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 1rem; }

img {
  display: block;
  max-width: 100%; }

a {
  text-decoration: none;
  color: var(--text-color); }

body {
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: var(--main-font);
  font-weight: 300; }

.container {
  margin-right: auto;
  margin-left: auto;
  padding-right: 1rem;
  padding-left: 1rem;
  max-width: var(--container-width);
  position: relative; }

[data-page='home'].hide {
  display: none; }

@keyframes showRight {
  from {
    opacity: 0;
    transform: translateX(-30px); }
  to {
    opacity: 1;
    transform: translateX(0); } }

@keyframes showPageRight {
  from {
    transform: translateX(-100%); }
  to {
    transform: translateX(0); } }

@keyframes showScale {
  from {
    transform: scale(0); }
  to {
    transform: scale(1); } }

@keyframes loadingSpinning {
  to {
    transform: rotate(1turn); } }

.header {
  background-color: var(--element-color);
  padding-top: 1.875rem;
  padding-bottom: 1.875rem;
  box-shadow: var(--main-shadow); }
  .header-container {
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .header-title {
    font-size: 1rem;
    font-weight: 800; }
    @media (min-width: 700px) {
      .header-title {
        font-size: 1.5rem; } }

.filters {
  padding-top: 3rem; }
  .filters-form {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    flex-wrap: wrap; }
    .filters-form > * {
      border: none;
      outline: none;
      box-shadow: var(--main-shadow);
      border-radius: 0.625rem;
      margin-bottom: 3rem; }
  .filters-text-search {
    width: 480px;
    max-width: 100%;
    max-width: 100%;
    padding: 1.375rem 1.5rem 1.375rem 4.5rem;
    color: var(--text-color);
    font-size: 0.875rem;
    font-weight: 300;
    background-color: var(--element-color);
    background-image: url("../../../images/search-icon.png");
    background-repeat: no-repeat;
    background-size: 18px;
    background-position: calc((4.5rem - 18px) / 2) center; }
    .filters-text-search::placeholder {
      color: var(--input-text-color); }
    .filters-text-search.dark-mode {
      background-image: url("../../../images/search-icon-white.png"); }
  .filters-region-wrapper {
    position: relative; }
    .filters-region-wrapper > * {
      border-radius: 0.625rem; }
  .filters-region-search {
    width: 200px;
    max-width: 100%;
    padding: 1.375rem 1.5rem;
    background-color: var(--element-color);
    color: var(--text-color);
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    font-size: 0.875rem;
    font-weight: 600; }
  .filters-region-list {
    display: none;
    position: absolute;
    top: 66px;
    z-index: 200;
    background-color: var(--element-color);
    padding: 1.375rem 1.25rem;
    width: 100%; }
    .filters-region-list.active {
      display: block;
      animation: showRight 0.3s forwards; }
  .filters-region-item {
    font-size: 0.875rem;
    font-weight: 600;
    cursor: pointer;
    line-height: 1;
    transition: all 0.3s; }
    .filters-region-item:not(:last-child) {
      margin-bottom: 1.125rem; }
    .filters-region-item:hover {
      color: var(--input-text-color); }

.countries {
  padding-bottom: 2.625rem; }
  .countries-list {
    display: grid;
    grid-template-columns: 100%;
    column-gap: 4.75rem;
    row-gap: 2.625rem;
    align-items: stretch; }
    @media (max-width: 499px) {
      .countries-list {
        margin-left: 2rem;
        margin-right: 2rem; } }
    @media (min-width: 500px) {
      .countries-list {
        grid-template-columns: repeat(2, 1fr); } }
    @media (min-width: 700px) {
      .countries-list {
        grid-template-columns: repeat(3, 1fr); } }
    @media (min-width: 1024px) {
      .countries-list {
        grid-template-columns: repeat(4, 1fr); } }
  .countries-item {
    border-radius: 0.5rem;
    overflow: hidden;
    box-shadow: var(--main-shadow);
    animation: showScale 0.3s forwards; }
  .countries-image {
    height: 160px;
    overflow: hidden; }
    .countries-image img {
      height: 100%;
      width: 100%;
      object-fit: cover;
      object-position: center; }
  .countries-information {
    background: var(--element-color);
    padding: 1.875rem 1.5rem;
    height: 100%; }
  .countries-title {
    margin-bottom: 1.5rem;
    font-weight: 800; }
  .countries-information-item {
    font-size: 0.875rem;
    line-height: 1.6; }
    .countries-information-item span {
      font-weight: 600; }
  .countries-error {
    grid-column: span 4;
    text-align: center;
    font-size: 1.5rem;
    font-weight: 600; }

.mode-switcher {
  color: var(--text-color);
  font-weight: 600;
  display: flex;
  align-items: center; }
  .mode-switcher i {
    margin-right: 0.5rem; }

.loading {
  padding: 0.125rem 1rem; }
  .loading-spinner {
    width: 40px;
    height: 40px;
    margin-left: auto;
    margin-right: auto;
    border: 0.25rem solid var(--element-color);
    border-top-color: var(--text-color);
    border-radius: 50%;
    animation: loadingSpinning 0.5s infinite; }
  .loading.hide {
    display: none; }

.details {
  padding-top: 2rem;
  padding-bottom: 2rem;
  display: none;
  border-right: 0.0625rem solid #000;
  position: relative; }
  @media (min-width: 900px) {
    .details {
      padding-top: 5rem;
      padding-bottom: 5rem; } }
  .details.active {
    display: block;
    animation: showPageRight 0.5s forwards; }
  @media (min-width: 900px) {
    .details-container {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 4rem;
      row-gap: 5rem;
      align-items: center; } }
  @media (max-width: 899px) {
    .details-image {
      margin: 0 auto 2rem auto; } }
  .details-button {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-column: 1 / 3;
    max-width: 135px;
    width: auto;
    outline: none;
    cursor: pointer;
    border: 0;
    background-color: var(--element-color);
    color: var(--text-color);
    padding: 0.75rem 1rem;
    border-radius: 0.25rem;
    font-size: 0.875rem;
    box-shadow: var(--button-shadow); }
    @media (max-width: 899px) {
      .details-button {
        margin-bottom: 4rem; } }
    .details-button i {
      margin-right: 0.625rem; }
  .details-title {
    font-size: 1rem;
    font-weight: 800;
    margin-bottom: 2.5rem; }
    @media (min-width: 600px) {
      .details-title {
        font-size: 1.5rem; } }
  .details-list {
    display: grid;
    grid-template-columns: 100%;
    column-gap: 2rem;
    row-gap: 1rem;
    margin-bottom: 4.75rem; }
    @media (min-width: 600px) {
      .details-list {
        grid-template-columns: repeat(2, 1fr);
        grid-auto-flow: dense; } }
  .details-item {
    font-size: 0.875rem; }
    .details-item span:first-of-type {
      font-weight: 800; }
    .details-item:nth-child(-n + 5) {
      grid-column: 1; }
    @media (max-width: 599px) {
      .details-item:nth-child(5) {
        margin-bottom: 5rem; } }
  @media (min-width: 600px) {
    .details-wrapper-borders {
      display: flex;
      align-items: center; } }
  .details-wrapper-borders span {
    font-weight: 800;
    font-size: 0.875rem;
    margin-right: 0.625rem; }
    @media (max-width: 599px) {
      .details-wrapper-borders span {
        display: block;
        margin-bottom: 2.875rem; } }
  .details-borders {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-gap: 0.625rem;
    justify-content: start; }
    @media (max-width: 899px) {
      .details-borders {
        grid-template-columns: repeat(3, auto);
        justify-content: center; } }
    @media (min-width: 1200px) {
      .details-borders {
        grid-template-columns: repeat(3, auto); } }
  .details-borders-item {
    background-color: var(--element-color);
    box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.2);
    padding: 0.625rem 1.25rem;
    border-radius: 0.25rem;
    font-size: 0.875rem;
    display: flex;
    justify-content: center;
    align-items: center; }
    .details-borders-item a {
      color: var(--text-color); }

.footer {
  padding-top: 2rem;
  padding-bottom: 2rem;
  text-align: center; }
  .footer > * {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap; }
  .footer-copy {
    font-size: 0.875rem;
    font-weight: 300;
    margin-bottom: 0.625rem; }
  .footer-author {
    font-size: 0.875rem;
    font-weight: 300;
    line-height: 1.6; }
  .footer a {
    font-weight: 800;
    display: block;
    margin: 0 0.25rem;
    background-color: var(--element-color);
    padding: 0.25rem 0.5rem;
    border-radius: 0.25rem;
    box-shadow: var(--main-shadow); }
